<template>
  <div class="pagination-wraper pagination-wraper--right">
    <span
      v-for="n in totalPages"
      :key="n" class="pagination"
      :class="{ 'active':  n == currentPageNumber }"
      @click="currentPage = n">
      <b>{{n}}</b>
    </span>
  </div>
</template>
<script>
export default {
  name: 'pagination',
  props: {
    pageNumber: {
      type: Number,
      default: 1
    },
    totalItems: {
      type: Number,
      default: 1
    },
    searchParams: {
      type: Object
    }
  },
  watch: {
    currentPageNumber () {
      this.fetchData()
    }
  },
  mounted () {
    this.fetchData()
  },
  data () {
    return {
      pageSize: 10,
      currentPageNumber: 1,
      hasNext: true,
      hasPrevious: false,
      nextPageNumber: 2,
      previousPageNumber: 1
    }
  },
  computed: {
    totalPages: {
      get () {
        return Math.ceil(this.totalItems / this.pageSize)
      }
    },
    params: {
      get () {
        return {
          page_size: this.pageSize,
          page: this.currentPageNumber
        }
      }
    },
    currentPage: {
      get () {
        return this.currentPageNumber
      },
      set (value) {
        this.currentPageNumber = value
      }
    }
  },
  methods: {
    fetchData () {
      this.$emit('pagination', this.params)
    }
  }
}
</script>



